<template>
  <div id="common-box">
    <div class="register-box">
        <h4>考生信息注册</h4>
        <el-form :model="ruleForm" :rules="rules"
            ref="ruleForm" label-width="150px" class="demo-ruleForm">
            <div class="form-left">
                <el-form-item label="真实姓名" prop="realName">
                    <el-input v-model="ruleForm.realName"></el-input>
                </el-form-item>
                <el-form-item label="考生类别">
                    <el-radio v-model="ruleForm.category" label="成教">成教</el-radio>
                    <el-radio v-model="ruleForm.category" label="网教">网教</el-radio>
                    <el-radio v-model="ruleForm.category" label="自考">自考</el-radio>
                </el-form-item>
                <el-form-item label="证件类型">
                    <el-select v-model="ruleForm.cardType" placeholder="请选择证件类型">
                        <el-option v-for="(item,index) in peopleOptions"
                            :key="index" 
                            :label="item.label"
                            :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="入学时间">
                    <div class="school">
                        <el-date-picker
                            v-model="ruleForm.enterDate"
                            type="date"
                            format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd"
                            placeholder="入学时间">
                        </el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item label="教学点（选填）" prop="studyAddr">
                    <el-select v-model="ruleForm.studyAddr" placeholder="请选择教学点">
                        <el-option v-for="(item,index) in studyAddrOptions"
                            :key="index" 
                            :label="item"
                            :value="item"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="照片">
                    <div class="photo-btn"
                        v-if="!isEdit">
                        <form ref="formphoto"
                            enctype="multipart/form-data">
                            <input type="file" accept="image/*"
                                @change="setImage">
                        </form>
                        <img :src="photoURL" class="upload-img">
                    </div>
                    <div class="photo-btn" v-else>
                        <img :src="ruleForm.photo | photoFilter" class="upload-img">
                    </div>
                </el-form-item>
            </div>
            <div class="form-right">
                <el-form-item label="性别">
                    <el-radio v-model="ruleForm.sex" label="男">男</el-radio>
                    <el-radio v-model="ruleForm.sex" label="女">女</el-radio>
                </el-form-item>
                <el-form-item label="联系电话" prop="phone">
                    <el-input v-model="ruleForm.phone"></el-input>
                </el-form-item>
                <el-form-item label="证件号码" prop="cardNum">
                    <el-input v-model="ruleForm.cardNum"></el-input>
                </el-form-item>
                <el-form-item label="毕业时间">
                    <div class="school">
                        <el-date-picker
                            v-model="ruleForm.graduationDate"
                            type="date"
                            format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd"
                            placeholder="毕业时间">
                        </el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item label="学历（选填）" prop="degree">
                    <el-select v-model="ruleForm.degree" placeholder="请选择学历">
                        <el-option v-for="(item,index) in degreeOptions"
                            :key="index" 
                            :label="item.label"
                            :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="工作单位（选填）" prop="workPlace">
                    <el-input v-model="ruleForm.workPlace"></el-input>
                </el-form-item>
                <el-form-item v-if="!isEdit">
                    <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
                    <el-button @click="resetForm('ruleForm')">取消</el-button>
                </el-form-item>
                <el-form-item v-else>
                    <el-button type="success" @click="editForm('ruleForm')">编辑</el-button>
                </el-form-item>
            </div>
        </el-form>
    </div>
  </div>
</template>

<script>
import URL from '@/axios/base.js'

export default {
    filters:{
        photoFilter(img){
            return URL.url+img;
        }
    },
    data(){
        const vaildateTel=(rule,value,callback)=>{
        let mytel=/^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
        if(!mytel.test(value)){
            callback(new Error('手机号格式错误'));
        }else{
            callback();
        }
        };
        return{
        isEdit:true,
        ruleForm:{
            cardType:'',
            cardNum:'',
            phone:'',
            realName:'',
            degree:'',
            workPlace:'',
            studyAddr:'',
            photo:'',
            enterDate:'',
            graduationDate:'',
            sex:'',
            category:''
        },
        rules:{
            cardNum:[
            { required:true,message:'请填写证件号码',trigger:'blur'},
            ],
            phone:[
            { required:true,message:'请填写联系电话',trigger:'blur'},
            { validator:vaildateTel,trigger:'blur'}
            ],
            realName:[
            { required:true,message:'请填写真实姓名',trigger:'blur'}
            ]
        },
        peopleOptions:[
            { label:'中华人民共和国居民身份证',value:'中华人民共和国居民身份证'},
            { label:'台湾居民往来大陆通行证',value:'台湾居民往来大陆通行证'},
            { label:'港澳居民往来大陆通行证',value:'港澳居民往来大陆通行证' },
            { label:'护照',value:'护照' },
            { label:'香港身份证',value:'香港身份证' },
            { label:'澳门身份证',value:'澳门身份证' },
            { label:'港澳居民居住证',value:'港澳居民居住证' },
            { label:'台湾居民居住证',value:'台湾居民居住证' }
        ],
        degreeOptions:[
            { label:'本科',value:'本科'},
            { label:'研究生',value:'研究生'},
            { label:'硕士',value:'硕士'},
            { label:'博士',value:'博士'}
        ],
        studyAddrOptions:[],
        photoURL:'',
        isCode:false,
        retime:'30'
        }
    },
    methods:{
        setImage(e){//选择图片
            let that=this;
            const file=e.target.files[0];
            const reader = new FileReader();
            const formData=new FormData();
            reader.readAsDataURL(file);
            reader.onload=(event)=>{
                that.photoURL=event.target.result;
            }
            formData.append('file',file);
            that.$api.upImg(formData).then(res=>{
                let data = res.data;
                if(data.err_code === 100){
                    that.ruleForm.photo = data.data.url;
                    that.$message.success(data.err_msg);
                }else{
                    return that.$message.error('上传图片失败');
                }
            })
        },
        editForm(){//编辑
            this.isEdit=false;
        },
        submitForm(formVal){//提交
            let that=this;
            that.$refs[formVal].validate((valid)=>{
                if(valid){
                    if(!that.ruleForm.photo){
                        return that.$message.error('请上传照片');
                    }
                    if(!that.ruleForm.enterDate){
                        return that.$message.error('请选择入学时间');
                    }
                    that.$api.testinfo({
                        cardNum:that.ruleForm.cardNum,
                        cardType:that.ruleForm.cardType,
                        degree:that.ruleForm.degree,
                        enterDate:that.ruleForm.enterDate,
                        graduationDate:that.ruleForm.graduationDate,
                        phone:that.ruleForm.phone,
                        photo:that.ruleForm.photo,
                        sex:that.ruleForm.sex,
                        realName:that.ruleForm.realName,
                        studyAddr:that.ruleForm.studyAddr,
                        workPlace:that.ruleForm.workPlace,
                        category:that.ruleForm.category
                    }).then(res=>{
                        if(res.err_code === 100){
                            that.$message.success('信息提交成功');
                            that.isEdit=false;
                        }else{
                            return that.$message.error('信息提交失败');
                        }
                    })
                }else{
                    return that.$message('未按要求填写,无法注册');
                }
            })
        },
        resetForm(){//取消
            this.isEdit=true;
        },
        getAddr(){//获取教学点
            this.$api.addr().then(res=>{
                this.studyAddrOptions=res.data.list;
            })
        },
        getStuInfo(){//考生信息
            let that=this;
            that.$api.studentinfo().then(res=>{
                if(res.data.info){
                    that.ruleForm = res.data.info;
                    that.isEdit=true;
                }
            })
        }
    },
    created(){
        this.getStuInfo();
        this.getAddr();
    }
}
</script>

<style lang="scss" scoped>
    .register-box{
        margin: 10px auto;
        width: 100%;
        height: 520px;
        background: #fff;
        box-sizing: border-box;
        padding: 20px;

        h4{
            margin-bottom: 20px;
        }

        .el-form{
            display: flex;
            justify-content: space-around;
            height: 100%;
            width: 100%;
        }
    }
    .upload-img{
        width:80px;
        height:100px;
        background: #f8f8f8;
        border: 1px solid #eee;
    }
</style>